@import "./snow.scss";
@import "./nav.scss";
@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@700&family=Raleway:wght@600&display=swap');

.App {
    display: flex;
    background-image: url("../assets/images/snowy_04.jpg");
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 100%;
    position: fixed;
}

h1 {
    font-family: 'Caveat', cursive;
    color: black;
    font-size: 5rem;
}

h2 {
    font-family: 'Caveat', cursive;
    color: black;
    font-size: 3rem;
}

@media only screen and (max-width: 768px) {
    h1 {
      font-size: 3rem;
    }

    h2 {
        font-size: 2rem;
    }
}

@media only screen and (max-width: 600px) {
    h1 {
      font-size: 3rem;
    }

    h2 {
        font-size: 1.3rem;
    }
}

@media only screen and (max-width: 400px) {
    h2 {
        font-size: 1rem;
    }
}





.flex-center {
    display: flex;
    justify-content: center;
}

.margin-0 {
    margin: 0;
}

.flex-column-100 {
    display: flex;
    flex-direction: column;
    width: 100%;
}