.navbar {
    display: flex;
    height: 95vh;
    margin: 15px 0 0 0;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.navbar img {
    margin: auto;
    width: 55px;
    cursor: pointer;
    opacity: 1;
}

.socials {
    display: flex;
    height: 20vh;
    flex-direction: column;
    justify-content: space-between;
}

